import task from "../../taksClass";

function AddTask(props) {

  const {text , func} = props.bigButton

  return ( 
  <div className="AddTask d-flex justify-content-between m-auto p-2">
    <button type="button" onClick={sortTasks} className="btn btn-lg btn-warning"></button>
    <button type="submit" onClick={func} className="btn btn-lg" id="register"></button>
    <button type="button" onClick={addTask} className="btn btn-lg btn-secondary"></button>
  </div>
  );


  function addTask(e) {
    const tasks = getStorage("tasks")
    tasks.push(new task())
    changeStorage("tasks",tasks)
  }

  function sortTasks(e) {
    const tasks = getStorage("tasks")
    tasks.sort((a,b)=>{
      return (a.status.length - b.status.length)
    })

    changeStorage("tasks",tasks)
  }


}

export default AddTask;