import { useState } from "react";

function TaskInput(props) {
  const tasks = props.tasks
  const task = props.task
  const taskIndex = props.index

  const [showing , changeFade] = useState("showing")
  const classNAme = `TaskInput ${showing}`

  return ( 
    <div className={classNAme}>
        <button className="btn btn-danger icon trash" onPointerUp={deleteTask}>
          <i class="fas fa-trash"></i>
          </button>

        <div className={"select"+ " TaskInputSelect "+task.status}>
            <select className="form-select" aria-label=".form-select-lg example"  
            onChange={handleStatusChange}>
                <option value={"wajib"} selected={task.status ==="wajib"} >wajib</option>
                <option value={"mousta7ab"} selected={task.status ==="mousta7ab"} >mousta7ab</option>
                <option value={"mamnou3"} selected={task.status ==="mamnou3"} >mamnou3</option>
            </select>
        </div>

        <input className="form-control fs-6 text-end"  onChange={handleNamrChange} value={task.name} required/>
      </div>
  );

 function update() {
  changeStorage("tasks",tasks)
 }

  function handleStatusChange(e) {
    task.status = e.target.value
    update()
  }

  function handleNamrChange(e) {
    task.name = e.target.value
    update()
  }

  function deleteTask() {
    tasks.splice(taskIndex,1)
    changeFade("")

    setTimeout(() => {
      changeFade("showing")
      update()
    }, 350);
    // 
  }
}

export default TaskInput;