import  moment from "moment"
import "moment/locale/ar-dz"
import ReactDOM from 'react-dom/client';

import { useEffect, useRef, useState } from "react";
import AddDay from "./AddDay";
import AllDays from "./AddDay/AllDays";
import LastDay from "./LastDay";
import { lastItem, matrixArray } from "../utils.js";
import ArrayScroller from "./ArrayScroller";

function MainPage(props) {


  const All_days = getStorage("days") 

  const [daysMatrix , setDaysMatrix] = useState( matrixArray( Array.from(All_days) , 10 ) )

  const [currentDays , setcurrentDays ] = useState( lastItem(daysMatrix) )


  useEffect(()=>{
    console.log("main page rerendered");
    setcurrentDays(lastItem(daysMatrix))
  } , [daysMatrix])

  let lastTimeMessage = (All_days.length > 0)?
        moment(lastItem(All_days)
                .date)
                  .fromNow()
        : "     ";




  const lastDayObj = All_days[[All_days.length - 1]]
  const lastDayElement = useRef()

  return ( 
    <div className="container-fluid pt-4 pb-4" id="main">
      <div className="container bg-warning epic-text rounded p-2 mb-3">
        <p className="text-center fs-3">              </p>
        <p className="mb-0">[:40-41]</p>
      </div>

      <AddDay setLastDayObj={updateLastDay} addDay={addDay} />

      <p className="last-time fs-3">
        {lastTimeMessage}
      </p>

      <div className="last-day-container container-fluid mt-3" ref={lastDayElement}>
        {lastDayObj ? <LastDay day={lastDayObj}/> : undefined}
      </div>

{All_days.length > 0?
<>
        <h1 className="mt-5 mb-2 text-light justify-content-end d-flex gap-1 align-items-center">
        <button className="btn secondary badge size-sm bg-light text-dark"
              data-bs-toggle="tooltip" data-bs-placement="top" 
              data-bs-html="true" data-bs-title=
                "<h4 class='bg-black text-light'>       </h4>">
                    ?
            </button>
           

        </h1>

        <ArrayScroller items={daysMatrix} clickFnc={updateMatrix} />
      <div className="all-days-grid bg-dark bg-gradient p-4 ">
        <AllDays lastDayElement={lastDayElement} days={currentDays}/>
      </div>
</> 
: undefined }

{/* <button onClick={e=>{
  console.log({currentDays , daysMatrix});
}}>Test</button> */}
    </div>

    
  );

  function updateLastDay() {
    const days = getStorage("days")
    setLastDayObj(days[days.length - 1])
  }

  function updateMatrix(item) {
    setcurrentDays(item)
  }

  function addDay() {
    Promise.resolve().then(()=>{
      setDaysMatrix(
        matrixArray( Array.from( getStorage("days") ) , 10 )
      )

    })
  }

}


export default MainPage;


