import { tick } from "../../sounds";

function AutoSelector(props) {
  const taskList = props.taskList
  const setTaskList = props.setTaskList

  let basicChecked = true ; let bonusChecked = true
  handleSwitchChcked()

  const audio = tick.cloneNode(true)

  return ( 
  <div className="auto-selector w-100 mt-3">
      <div className={"form-check form-switch"}>
        <input className="form-check-input" onChange={completeWajibat}  type="checkbox" role="switch" id="finish-wajib" checked={basicChecked} />
        <label className="form-check-label" htmlFor="finish-wajib" >  </label>
      </div>

      <div className={"auto-selector form-check form-switch"}>
        <input className="form-check-input" onChange={completeMousta7abat}  type="checkbox" role="switch" id="finish-mousta7ab" checked={bonusChecked} />
        <label className="form-check-label" htmlFor="finish-mousta7ab" >  </label>
      </div>
  </div> );

    function update() {
      setTaskList(
        Array.from(taskList)
      )
    }
  function handleSwitchChcked() {
    taskList.forEach(task =>{
      if(task.completed) return
    // task not completed

    if(task.status == "wajib" || task.status == "mamnou3"){
      basicChecked = false
    }
    else if(task.status = "mousta7ab"){
      bonusChecked = false
    }
    })
  }

  function completeWajibat() {
    playAudio()

    taskList.forEach(task =>{
      if (task.status == "mousta7ab") return
      task.completed = true
    })
    update()
  }

  function completeMousta7abat() {
    playAudio()
    
    taskList.forEach(task =>{
      if (task.status == "mousta7ab") {
      task.completed = true
      }
    })
    update()
  }

  function playAudio() {
    audio.play() ; audio.remove()
  }
  

}

export default AutoSelector;