import moment from "moment";
import { randomizeAudioSpeed, separateTasks } from "../utils";
import LastdayStaticTask from "./LastdayStaticTask";
import { page_flip } from "../sounds";

function LastDay(props) {

  const {tasks,score,id,date}  = props.day
  const formated_date = moment( date ).format("LLLL")
  
  const taskObj = separateTasks(tasks)
  const static_wajib = taskObj.wajib.map(task => <LastdayStaticTask key={task.name} task={task}/>)
  const static_mamnou3 = taskObj.mamnou3.map(task => <LastdayStaticTask key={task.name} task={task}/>)
  const static_mousta7ab = taskObj.mousta7ab.map(task => <LastdayStaticTask key={task.name} task={task}/>)

  const audio = page_flip.cloneNode(false)
  audio.volume = 0.45
  randomizeAudioSpeed(audio)
  audio.play()


  return (
 <div className="last-day card">

  <div className="card-header d-flex flex-wrap flex-row-reverse
            fs-4 bg-primary-bs text-white">
        <p className="last-day-date text-end flex-grow-1">{formated_date}</p>

        <p className="last-day-score flex-grow-1">Score :
        <span className={"score-percent" + (score > 100?" bonus-score-percent":" basic-score-percent") + (score > 0?" colored":" white") }>
            {score}%
        </span>
        </p>
  </div>


  <div className="card-body d-flex flex-wrap flex-row-reverse gap-4">
    <div className="flex-grow-1 m-auto border border-dark">
      <div className="static-wajib-tasks p-3">
          {static_wajib}
      </div>

      <div className="static-mamnou3-tasks p-3">
        {static_mamnou3}
      </div>

    </div>


    <div className="static-mousta7ab-tasks h-100 p-3 border border-dark
    flex-grow-1 m-auto">
      {static_mousta7ab}
    </div>

  </div>

  
  </div> );
}

export default LastDay;