
function Navbar(props) {

  const pfp_index = Math.ceil(Math.random() * 4)
  const name = getStorage("name")

  const spentDays= getStorage("days").length
  

  return ( 
    <nav className="container-fluid bg-dark  text-light p-4 d-flex justify-content-center align-items-center" id="nav">

      <p className="daysLeft">{spentDays} / 90</p>

        <div className={"gc"+pfp_index} id="gc-pfp"> </div>
        <h1>{name}</h1>

      <button className="btn bg-primary-bs " onClick={unregister}>
      <i className="fas fa-pencil-alt"> </i>
      </button>

    </nav>
   );

   function unregister(e) {
    changeStorage("registered",false)
   }
}

export default Navbar;