import { calculateCompletePercentage, matrixArray, oneAudio } from "../../utils";
import moment from "moment";
import "moment/locale/ar-dz"
import { success } from "../../sounds";

function AddDayFooter(props) {
    const taskList = props.taskList
    const setLastDayObj = props.setLastDayObj

    const prev_days = getStorage("days")
    const {basicP ,bonus} = calculateCompletePercentage(taskList)

  return ( 
  <>
        <button type="button" className="btn btn-secondary no-ff" data-bs-dismiss="modal"></button>
        <button type="button" className="btn btn-success no-ff" data-bs-dismiss="modal"
          onClick={e => { if((basicP + bonus) != 0){addDayToStorage() ; playAudio()}  }}
        ></button>
  </> );

  function addDayToStorage() {

    const current_date = moment()
    current_date.locale("ar-dz").format("LL")

    prev_days.push(
    { tasks:  taskList,
      score: basicP + bonus ,
      id: prev_days.length ,
      date: current_date
    })
    
    changeStorage("days",prev_days)

    props.addDay()



    setTimeout(props.resetTasks, 500);
  }

  function playAudio(params) {
    const audio = oneAudio([success])
    audio.volume = 0.6
    audio.play()
  }
}

export default AddDayFooter;