function LastdayStaticTask(props) {
  const task = props.task
  const taskCompleted = task.completed? true: false ;

  return ( 
    <p className="d-flex align-items-center justify-content-between">
      <input className="form-check-input mt-0" type="checkbox"  aria-label="Checkbox for following text input" value=""  checked={taskCompleted} onChange={e=>{e.preventDefault();e.target.checked = taskCompleted }} />
      {task.name}
    </p>
);

}

export default LastdayStaticTask;