export function separateTasks(taskList) {
  const wajib = []
  const mousta7ab = []
  const mamnou3 =[]

  taskList.forEach((task)=>{
    if (task.status == "wajib") {
      wajib.push(task)
    }

    else if(task.status == "mousta7ab"){
      mousta7ab.push(task)
    }

    else if(task.status == "mamnou3"){
      mamnou3.push(task)
    }
  })


return {wajib,mousta7ab,mamnou3}
}//gives task obj

export function calculateCompletePercentage(taskList) {
  const tasksObj = separateTasks(taskList)
  const basic = [...tasksObj.wajib , ...tasksObj.mamnou3]
  const bonus = [...tasksObj.mousta7ab]


  const total_basic = basic.length
  const total_bonus = bonus.length
  const completed_basics = basic.filter((task)=>task.completed).length
  const completed_bonus = bonus.filter((task)=>task.completed).length

  const completed_basics_percentage = (completed_basics * 100) / total_basic
  const completed_bonus_percentage = completed_bonus * 20



  return {basicP: Math.ceil(completed_basics_percentage) ,
          bonus: Math.ceil(completed_bonus_percentage)}
}

export function bonusTaskValue(taskObj) {
  const bonus = [...taskObj.mousta7ab]
  let bonusValue = 0
  bonus.forEach(task=>{
    if (task.completed) {
      bonusValue+= 20
    }

  })

  return bonusValue
}

// array
  export function matrixArray(array,size=10) {
    const matrix = []

    while(array.length > 0){
      matrix.push(
        array.splice(0,size)
      )
    }

    return matrix
  }

  export function lastItem(array) {
    let item = array[array.length - 1]
    if (item == undefined) return []
    return item
  }

// audio
export function randomizeAudioSpeed(audio){
  audio.playbackRate = (0.85 + Math.random() * 0.35)
}

export function oneAudio(audioArray = ["audio"]) {
  const i = Math.floor(Math.random() * audioArray.length)
  return audioArray[i]
}