function NameInput() {
  let name = getStorage("name")
  return ( 
<div className="input-group mx-auto mt-3 mb-3" id="inputNameGroup">
  <div className="form-floating">
    <input onChange={changeName} value={name} type="text" className="form-control" id="inputName" placeholder="Username" required/>
    <label className="text-dark" htmlFor="inputName">Gigachad :</label>
  </div>
  <span className="input-group-text">: </span>

</div>

   );


   function changeName(e) {
    changeStorage("name",e.target.value)
   }
}



export default NameInput;