import moment from "moment";
import LastDay from "../LastDay";
import ReactDOM from 'react-dom/client';


function SingleDay(props) {
  const lastDayElement=props.lastDayElement
  
  const {id,date,score} = props.day
  let leftBasic , leftBonus ;
    if (score > 100) {
      leftBasic=100
      leftBonus=score - 100
    }
    else{
      leftBasic=score
      leftBonus = 0
    }

    const totalPercent = leftBasic + leftBonus

  return ( 
    <div className="single-day" onClick={updateLastDay}>
      <div className="date-and-id">
        <span className="day-id">{id + 1}</span>
        <span className="day-date">{moment(date).format("DD/MM/YYYY")}</span>
      </div>

      <p className="mb-0 mt-1 text-light">Score: 
          <span className={"score-percent" + (totalPercent > 100?" bonus-score-percent":" basic-score-percent") + (totalPercent > 0?" colored":" white") }>
            {totalPercent}%
          </span>
      </p>
      <div className="score-bar mt-0"
        style={{
        "--basic":leftBasic + "%",
        "--bonus":leftBonus + "%"
      }}>

          <div className="score-bar-basic"></div>
          <div className="score-bar-bonus"></div>
      </div>

    </div>
   );



   function updateLastDay() {

    const root = ReactDOM.createRoot(lastDayElement.current)
    root.render(<LastDay day={props.day}/>)
   }
}

export default SingleDay;