import TaskInput from "./TaskInput";


function TaskEditMenu(props) {
  const bareTasks = getStorage("tasks")
  const taskEditElems = bareTasks.map((task,index) => <TaskInput tasks={bareTasks} task={task} index={index} key={index} />)
  return ( <>
    {taskEditElems}
  </> );
}

export default TaskEditMenu;

