import { useEffect, useState } from "react";
import AddDayFooter from "./AddDay/addDayFooter";
import AutoSelector from "./AddDay/AutoSelector";
import Score from "./AddDay/Score";
import TaskChecker from "./AddDay/TaskChecker";

function AddDay(props) {
  let tasks = getStorage("tasks")
  tasks = sortTasks(tasks)
  const [taskList , setTaskList] = useState(tasks)



  const setLastDayObj = props.setLastDayObj


  return ( 
  <div>
    <button type="button"  className="btn btn-lg" id="addDayBtn" data-bs-toggle="modal" data-bs-target="#addDayModal"> </button>


<div className="modal fade" id="addDayModal" data-bs-backdrop="static" data-bs-keyboard="false" tabIndex="-1" aria-labelledby="staticBackdropLabel" aria-hidden="true">
  <div className="modal-dialog">
    <div className="modal-content fs-5 all">
      <div className="modal-header header flex-wrap">
          {/* Header */}
      <div className="text-end f-basis-100">
        <button type="button" className="btn-close bg-danger" data-bs-dismiss="modal" aria-label="Close"></button>
      </div>
        <Score taskList={taskList}/>
        <AutoSelector taskList={taskList} setTaskList={setTaskList} />
      </div>




      <div className="modal-body body">
        <TaskChecker taskList={taskList} setTaskList={setTaskList}/>
      </div>





      <div className="modal-footer footer">
        {/* Footer */}
        <AddDayFooter taskList={taskList} resetTasks={resetTasks} addDay={props.addDay}/>
      </div>
    </div>
  </div>
</div>

    
  </div> );






function sortTasks(tasks) {
  tasks.sort((a,b)=>{
    return (a.status.length - b.status.length)
  })

  return tasks

}

function resetTasks(params) {
  const base_tasks = getStorage("tasks")
  setTaskList(base_tasks)
}

}

export default AddDay;