import { calculateCompletePercentage, separateTasks } from "../../utils";

function Score(props) {

  const tasksList = props.taskList
  const {basicP , bonus} = calculateCompletePercentage(tasksList)



  let leftBasic = basicP , leftBonus = bonus;


  if (basicP < 100) {
    let missingP = 100 - leftBasic
    const bonus_has_enaugh = ((bonus - missingP) > 0)
    
    if (bonus_has_enaugh) {
      leftBasic = 100
      leftBonus -= missingP
    }
    else if (!bonus_has_enaugh) {
      leftBasic += leftBonus
      leftBonus = 0 
    }
  }


  const totalPercent = leftBasic + leftBonus



  return ( 
  <div className="score " >
    <h1 className="border border-3 border-dark text-center rounded-3">Score : 
      <span className={"score-percent" + (totalPercent > 100?" bonus-score-percent":" basic-score-percent") + (totalPercent > 0?" colored":" white") }>
        {totalPercent}%
      </span>
    </h1>
    Normal:<span className={"score-percent" +  (basicP > 0?" basic-score-percent":" white") }
    >{basicP}</span>% |
    bonus:<span className={"score-percent" + (bonus > 0?" bonus-score-percent":" white") }
    >{bonus}</span>%
    <br />
  </div> );
}

export default Score;

