import { tick } from "../../sounds";

function TaskCheckInput(props) {

  const task = props.task
  const taskList = props.taskList
  const setTaskList = props.setTaskList
  const taskCompleted = task.completed? true:false;
  const audio = tick.cloneNode(true)

  return ( 
      <div className={"daily-task form-check form-switch text-end"+ (" " + task.status + "-task") + (taskCompleted ? " completed" : " not-completed") }>
        <input className="form-check-input" onChange={handleCheck} type="checkbox" role="switch" id={task.name} checked={taskCompleted} value="troll"/>
        <label className="form-check-label w-100" htmlFor={task.name}>{task.name}</label>
      </div>
  );

  function handleCheck(e) {
    audio.play() ; audio.remove()
    const checked = e.target.checked

    if (checked) {task.completed = true }
      else       {task.completed = false}

      const newArr = Array.from(taskList)
      setTaskList(newArr)
  }
}

export default TaskCheckInput;