import { useEffect, useState } from "react";
import TaskCheckInput from "./TaskCheckInput";

function TaskChecker(props) {
  let taskList = props.taskList
  const setTaskList = props.setTaskList
  const taskElementList = taskList.map((task,index)=>{
          return <TaskCheckInput task={task} taskList={taskList} setTaskList={setTaskList} key={index} />}
  )

  return ( 
  <div className="TaskChecker">
      {taskElementList}
  </div> );




}

export default TaskChecker;