function ArrayScroller(props) {
  const items = props.items
  const clickFnc = props.clickFnc

    const itemsElems = items.map((item,index) => <button 
      className="btn bg-dark text-light"
      onClick={e => {clickFnc(item,index)}}
      key={index}>
        {index + 1}
      </button>)
  return ( 
  <div className="array-scroller bg-dark">
    {itemsElems}
  </div>
    );
}

export default ArrayScroller;